import React, {useState} from 'react'
import img1 from '../../img/photoGallery/item17.jpg'
import img2 from '../../img/photoGallery/item18.jpg'
import img3 from '../../img/photoGallery/item19.jpg'
import img4 from '../../img/photoGallery/item20.jpg'
import img5 from '../../img/photoGallery/item21.jpg'
import img6 from '../../img/photoGallery/item22.jpg'
import img7 from '../../img/photoGallery/item23.jpg'
import img8 from '../../img/photoGallery/item24.jpg'
import img9 from '../../img/photoGallery/item25.jpg'

import ImageCard from '../main/ImageCard'
export default function Photo() {
  const [data, setData] = useState([
    {
      id: 1,
      url: img1,
      title: "",
    },
    {
      id: 2,
      url: img2,
      title: " ",
    },
    {
      id: 3,
      url: img3,
      title: "",
    },
    {
      id: 4,
      url: img4,
      title: " ",
    },
    {
      id: 5,
      url: img5,
      title: "",
    },
    {
      id: 6,
      url: img6,
      title: "",
    },
    {
      id: 7,
      url: img7,
      title: " ",
    },
    {
      id: 8,
      url: img8,
      title: " ",
    },
    {
      id: 9,
      url: img9,
      title: "  ",
    },
  ])
  
  return (
    <section id='photo' className='photoGallery photoGallery_dark'>
      <h2 className="photoGallery__title photoGallery__title_dark" id='photo'></h2>
      <hr className="photoGallery__hr photoGallery__hr_dark" />
      <p className="photoGallery__description photoGallery__description_dark">      </p>
      <div className="photoGallery__content">
        {data.map((item, index) => 
            <ImageCard key={index} data={item} mode={"dark"}/>
        )}
      </div>
    </section>
  )

}
