import React, { useState } from 'react'
import AboutCityCard from '../main/AboutCityCard'

export default function AboutCity() {
  const [data, setData] = useState([
    {
      id: 1,
      title: "     2010",
      description: "        2010        "
    },
    {
      id: 2,
      title: "    2017",
      description: "         2017.       , , ,  ."
    },
    {
      id: 3,
      title: "   ",
      description: "             31  2008      "
    },
    {
      id: 4,
      title: "      ",
      description: "   11    .       , ,    "
    },
  ])
  
  return (
    <section className='aboutCity' >
      <h2 className="aboutCity__title" id='photo'> </h2>
      <hr className="aboutCity__hr" />
      <p className="aboutCity__description">    </p>
      <div className="aboutCity__content">
        {data.map((item, index) => 
            <AboutCityCard data={item}/>
        )}
      </div>
    </section>
  )

}
